import React from "react";
import { Button } from "../../../components/buttons/buttons";
import { Col, Form, Input, Modal, Row, Select } from "antd";
import { useDispatch } from "react-redux";
import { Constants } from "../../../config/constants";
import { phoneFormatted } from "../../../utility/utility";

const LeadsFilter = (
    {
        filter,
        state,
        setState,
        initialFilters,
        filters,
        setFilters,
        isLoading,
        fetchUsers,
        fetchCities,
        users,
        cities,
    }
) => {
    const dispatch = useDispatch()
    const layout = { labelCol: { span: 8 }, wrapperCol: { span: 16 } }
    const validateMessages = { required: '${label} is required!' };

    const handleSubmit = async (data) => {
        await setFilters({
            ...filters,
            ...data,
            page: 1,
            users: data.users.toString(),
            productName: data.name.toString(),
            from: data.from.toString(),
            to: data.to.toString(),
            sort: data.sort.toString(),
            status: data.status.toString(),
        })
        await setState({ ...state, filterVisible: false })
    };

    const resetFilters = async () => {
        await setFilters(initialFilters);
        await filter.resetFields();
    }

    const footerButtons = [
        <Button
            key="reset-button"
            htmlType="button"
            type="secondary"
            disabled={isLoading}
            size="medium"
            onClick={resetFilters}
        >
            {isLoading ? 'Loading...' : 'Reset'}
        </Button>,
        <Button
            form="leadsFilterForm"
            key="submit-button"
            htmlType="submit"
            type="primary"
            disabled={isLoading}
            size="medium"
        >
            {isLoading ? 'Loading...' : 'Submit'}
        </Button>
    ];

    return (
        <Col md={12}>
            <Modal
                type={"primary"}
                title="Leads Filter"
                visible={state.filterVisible}
                onCancel={() => setState({ ...state, filterVisible: false })}
                footer={footerButtons}
                width={800}
            >
                <Form
                    {...layout}
                    name={'leadsFilter'}
                    form={filter}
                    id={'leadsFilterForm'}
                    className="my-form"
                    validateMessages={validateMessages}
                    onFinish={handleSubmit}
                >
                    <Row>
                        <Col md={12}>
                            <Form.Item
                                name="users"
                                initialValue={filters?.users ? filters.users.split(",") : []}
                                rules={[{ required: false }]}
                                label="Users"
                            >
                                <Select
                                    allowClear
                                    showSearch
                                    mode="multiple"
                                    loading={isLoading}
                                    filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                    filterSort={(optionA, optionB) => optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())}
                                    onSearch={(event) => event?.length && event.length > 3 ? dispatch(fetchUsers({ search: phoneFormatted(event) })) : null}
                                >
                                    {users.length && users.map((user, key) => (
                                        <Select.Option value={user._id} key={key}>{`${user.firstName} ${user.lastName} - 0${user.phone.phone} - ${user.email}`}</Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="status"
                                initialValue={filters?.status ? filters.status : ""}
                                rules={[{ required: false }]}
                                label="Status"
                            >
                                <Select
                                    allowClear
                                    loading={isLoading}
                                    initialValue={filters?.status ? filters.status : ""}
                                >
                                    {Constants.LEAD_STATUS.map((status, key) => (
                                        <Select.Option value={status.value} key={key}>{status.label}</Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="from"
                                initialValue={filters?.from ? filters.from.split(",") : []}
                                rules={[{ required: false }]}
                                label="From"
                            >
                                <Select
                                    allowClear
                                    showSearch
                                    loading={isLoading}
                                    filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                    filterSort={(optionA, optionB) => optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())}
                                    onSearch={(event) => event?.length && event.length > 3 ? dispatch(fetchCities({ name: event })) : null}
                                >
                                    {cities.length && cities.map((city, key) => (
                                        <Select.Option value={city._id} key={key}>{city.name}</Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="to"
                                initialValue={filters?.to ? filters.to.split(",") : []}
                                rules={[{ required: false }]}
                                label="To"
                            >
                                <Select
                                    allowClear
                                    showSearch
                                    loading={isLoading}
                                    filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                    filterSort={(optionA, optionB) => optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())}
                                    onSearch={(event) => event?.length && event.length > 3 ? dispatch(fetchCities({ name: event })) : null}
                                >
                                    {cities.length && cities.map((city, key) => (
                                        <Select.Option value={city._id} key={key}>{city.name}</Select.Option>
                                    ))}
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="leadId"
                                initialValue={filters?.leadId ? filters.leadId : ""}
                                rules={[{ required: false }]}
                                label="Lead ID"
                            >
                                <Input placeholder="Lead ID" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="name"
                                initialValue={filters?.productName ? filters.productName : ""}
                                rules={[{ required: false }]}
                                label="Product Name"
                            >
                                <Input placeholder="Product Name" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="leadDateFrom"
                                rules={[{ required: false }]}
                                initialValue={filters?.leadDateFrom ? filters.leadDateFrom : null}
                                label="Lead From"
                            >
                                <Input type={"date"} placeholder="Lead Date From" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="leadDateTo"
                                rules={[{ required: false }]}
                                initialValue={filters?.leadDateTo ? filters.leadDateTo : null}
                                label="Lead To"
                            >
                                <Input type={"date"} placeholder="Lead Date To" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="updatedDateFrom"
                                rules={[{ required: false }]}
                                initialValue={filters?.updatedDateFrom ? filters.updatedDateFrom : null}
                                label="Updated From"
                            >
                                <Input type={"date"} placeholder="Updated Date From" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="updatedDateTo"
                                rules={[{ required: false }]}
                                initialValue={filters?.updatedDateTo ? filters.updatedDateTo : null}
                                label="Updated To"
                            >
                                <Input type={"date"} placeholder="Updated Date To" />
                            </Form.Item>
                        </Col>
                        <Col md={12}>
                            <Form.Item
                                name="sort"
                                initialValue={filters?.sort ? filters.sort : ""}
                                rules={[{ required: false }]}
                                label="Sort"
                            >
                                <Select
                                    allowClear
                                >
                                    <Select.Option value="">Default</Select.Option>
                                    <Select.Option value="weightDesc">Weight - Low to High</Select.Option>
                                    <Select.Option value="weightAsc">Weight - High to Low</Select.Option>
                                    <Select.Option value="costDesc">Cost - Low to High</Select.Option>
                                    <Select.Option value="costAsc">Cost - High to Low</Select.Option>
                                    <Select.Option value="priceDesc">Price - Low to High</Select.Option>
                                    <Select.Option value="priceAsc">Price - High to Low</Select.Option>
                                    <Select.Option value="createdAtDesc">CreatedAt - New To Old</Select.Option>
                                    <Select.Option value="createdAtAsc">CreatedAt - Old To New</Select.Option>
                                    <Select.Option value="updatedAtDesc">UpdatedAt - New To Old</Select.Option>
                                    <Select.Option value="updatedAtAsc">UpdatedAt - Old To New</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
            </Modal>
        </Col>
    )
}

export default LeadsFilter;
